import noData from '@/components/noData'
import back from '@/components/back'

export default {
    components:{
        noData,
        back
    },
    data(){
        return {
            page: 1,
            haveMore: true,
            list: [],
            pageShow: false
        }
    },
    created(){
        this.pull(false)
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods: {
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
        pull(h){
            if(this.haveMore){
                this.$post('Prescription/Prescription/GetPrescriptionListByPatientId',{
                    Status: -1,
                    PatientId: 1,
                    PageIndex: this.page,
                    PageSize: 15
                }).then(o => {
                    this.pageShow = true
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.list.push(v)
                        })
                    }
                    else {
                        this.list = o.ReturnData
                    }
                })
            }
        },
        toPre(idx){
            this.$store.dispatch('pushupList',this.list[idx].ListDetails)
            this.$store.dispatch('pushprescription',this.list[idx])
            this.$router.push(`/prescription?ApplyId=${this.list[idx].ApplyId}`)
        }
    }
}



